.contenedorDeLista {
    width: 500px;
    height: 500px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}
.formulaDeLista{
    width: 100%;
    height: 40px;
    padding: 15px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    gap: 10px;
}
.formulaDeLista > .inputDeLista {
    width: 100%;
    height: inherit;
    border-radius: 5px;
    border: 2px solid black;
    font-size: 20px;
}
.formulaDeLista > .buttonDeLista {
    background-color: cadetblue;
    border-radius: 5px;
    border: 2px solid black;
    width: 100px;
    height: inherit;
}
.formulaDeLista > .buttonDeLista:hover {
    background-color:blueviolet;
}
.contenedorDeListas{
    width: 100%;
    padding: 15px;
    display: grid;
    grid-template-columns: auto;
    grid-row-gap: 10px;
}
.contenedorDeListas > .lista >.todoInfo {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: center;
    gap: 5px;
    
}
.titulo{
    width: 100%;
    border-radius: 5px;
    font-size: 20px; 
}
.button{
    background-color:brown;
    border-radius: 5px;
    border: 2px solid black;
    width: 100px;
    height: 23px;
    color: white

}
